<template>
    <div class="position-relative">
            <!-- shape Hero -->
            <section class="section-shaped my-0">
                <div class="shape shape-style-1 shape-default shape-skew">
                </div>
                <div class="container shape-container d-flex">
                    <div class="col px-0">

                        <div class="row">
                            <div class="col-lg-2"></div>
                            <div class="col-lg-8">


                                <h1 class="display-3  text-white text-center">Hello! I'm Mark Lachica.
                                    <span> </span>
                                </h1>
                                <!--For typing text--> 
                                <div class="container text-center">
                                    <h2 class="typing-text">
                                    <span class="typed-text">{{ typeValue }}</span>
                                    <span class="cursor" :class="{'typing': typeStatus}">&nbsp;</span>
                                    </h2>
                                </div>
                                <p class="lead  text-white text-center">Creating modern and responsive design for Web and Mobile.</p>
                                <br>
                            </div>
                            <div class="col-lg-2"></div>
                        </div>
                    </div>
                </div>
            </section>
            <!-- 1st Hero Variation -->
        </div>
</template>
<script>
import * as timers from 'timers';
  export default {
    data: () => {
      return {
        _typeValue: '',
        get typeValue() {
          return this._typeValue;
        },
        set typeValue(value) {
          this._typeValue=value;
        },
        _typeStatus: false,
        get typeStatus() {
          return this._typeStatus;
        },
        set typeStatus(value) {
          this._typeStatus=value;
        },
        typeArray: ['A Creative Web Designer', 'A Front-End Developer'],
        typingSpeed: 200,
        erasingSpeed: 100,
        newTextDelay: 2000,
        typeArrayIndex: 0,
        charIndex: 0
      }
    },
    methods: {
      typeText() {
        if(this.charIndex < this.typeArray[this.typeArrayIndex].length) {
          if(!this.typeStatus)
            this.typeStatus = true;
          this.typeValue += this.typeArray[this.typeArrayIndex].charAt(this.charIndex);
          this.charIndex += 1;
          timers.setTimeout(this.typeText, this.typingSpeed);
        }
        else {
          this.typeStatus = false;
          timers.setTimeout(this.eraseText, this.newTextDelay);
        }
      },
      eraseText() {
        if(this.charIndex > 0) {
          if(!this.typeStatus)
            this.typeStatus = true;
          this.typeValue = this.typeArray[this.typeArrayIndex].substring(0, this.charIndex - 1);
          this.charIndex -= 1;
          timers.setTimeout(this.eraseText, this.erasingSpeed);
        }
        else {
          this.typeStatus = false;
          this.typeArrayIndex += 1;
          if(this.typeArrayIndex >= this.typeArray.length)
            this.typeArrayIndex = 0;
          timers.setTimeout(this.typeText, this.typingSpeed + 1000);
        }
      }
    },
    created() {
      timers.setTimeout(this.typeText, this.newTextDelay + 200);
    }
  }
</script>
<style lang="scss" scoped>
  .typing-text {
    font-weight: normal;
    span.typed-text {
      color: #ffcf01;
    }
    span.cursor {
      display: inline-block;
      margin-left: 3px;
      width: 4px;
      background-color: #fff;
      animation: cursorBlink 1s infinite;
    }
    span.cursor.typing {
      animation: none;
    }
  }

  .display-3 {
    font-size: 3rem;
  }

  @keyframes cursorBlink {
    49% { background-color: #fff; }
    50% { background-color: transparent; }
    99% { background-color: transparent; }
  }

  @media (max-width: 991px) {
    .section-shaped .shape.shape-skew + .shape-container {
      padding-bottom: 5rem;
    }
  }
</style>
